<template>
<section class="hero is-light is-bold is-fullheight">
    <!-- Hero head: will stick at the top -->
  <div class="hero-head">
    <span class="is-pulled-right px-4 mt-5 up-contacto" @click="open('#contacto', '_self')">
        Contacto
    </span>

    <span class="is-pulled-right px-4 mt-5 up-github" @click="open('https://github.com/decacis/')" title="GitHub">
        <i class="fab fa-github"></i>
    </span>
  </div>

  <div class="hero-body">
    <div class="container has-text-centered">
      <p class="subtitle is-6 specialTitle">
        <span>DANIEL CASTELLANOS</span>
      </p>
      <p class="subtitle is-2 sub-no-bottom">
          <span>
              {{ currentWord }}
          </span>
      </p>
    </div>
  </div>
</section>
</template>

<script>
import wordList from '@/data/hero-words.js'

export default {
  name: 'BulmaHero',
  data(){
      return {
          wordDurationMs: 55,
          wordList,
          currentWordIndex: 0,
          currentWord: wordList[0]
      }
  },
  methods: {
      changeWord(wordLength, mode){
        //   console.log(wordLength)
        //   console.log(mode)
          switch (mode) {
              case 'write':
                    if(wordLength > -1 && wordLength <= this.wordList[this.currentWordIndex].length){
                        this.currentWord = wordLength > 0 ? this.wordList[this.currentWordIndex].substr(0,wordLength) : ' ';
                        setTimeout(() => {
                            this.changeWord(wordLength+1, 'write');
                        }, this.wordDurationMs);
                    }else{
                        // (this.currentWordIndex === this.wordList.length) ? this.currentWordIndex -= 1 : this.currentWordIndex += 1;
                        setTimeout(() => {
                            this.changeWord(wordLength-1, 'erase');
                        }, 1800);
                    }
                  break;
          
              case 'erase':
                if(wordLength === 0){
                    (this.currentWordIndex === this.wordList.length-1) ? this.currentWordIndex = 0 : this.currentWordIndex += 1;
                    this.changeWord(0, 'write');
                }else{
                    this.currentWord = this.wordList[this.currentWordIndex].substr(0,wordLength);
                    setTimeout(() => {
                        this.changeWord(wordLength-1, 'erase');
                    }, this.wordDurationMs-5);
                }
                break;
          }
      },
      open(whereTo, mode = '_blank'){
          window.open(whereTo, mode);
      }
  },
  mounted: function(){
      setTimeout(() => {
        this.changeWord(this.currentWord.length, 'erase');
      }, 3000);
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Lora&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Barlow&display=swap');
.specialTitle{
    font-family: 'Barlow', sans-serif;
    font-weight: 400;
    margin-bottom: .2em !important;
}
.specialTitle span{
    padding: 0 .1em;
    background-color: #f48e5c;
}
.sub-no-bottom{
    font-family: 'Lora', serif;
    margin-bottom: .2em !important;
}
.sub-no-bottom span {
    padding-right: .1em;
    border-right: none;
    margin-right: 2px;
    animation-name: cursorText;
    animation-duration: 1.2s;
    animation-direction: normal;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-timing-function: steps(2, end);
}

.hero-head span{
    font-family: 'Barlow', sans-serif;
    font-weight: 400;
    right: 0;
    position: fixed;
    background-color: rgba(0, 0, 0, .5);
    cursor: pointer;
    color: whitesmoke;
    transition: .3s;
    z-index: 2;
}

.up-github{
    margin-top: 3.8rem !important;
}

.hero-head span:hover{
    background-color: rgba(0, 0, 0, .8);
}

@keyframes cursorText {
  0% {
      border-right: none;
      margin-right: 2px;
    }
  100% {
      border-right: 2px solid black;
      margin-right: 0;
    }
}
</style>
